import string from "./css";

let n = 0;
demo.innerText = string.substr(0, n);
demo2.innerHTML = string.substr(0, n);

let time = 100;

const run = () => {
  n += 1;
  if (n > string.length) {
    window.clearInterval(id);
    return;
  }
  console.log(n + ":" + string.substr(0, n));
  demo.innerText = string.substr(0, n);
  demo2.innerHTML = string.substr(0, n);
  demo.scrollTop = demo.scrollHeight; // å¸ƒå±€å¯æ»šåŠ¨æœ€å¤§é«˜åº¦
};

let id = setInterval(run, time);

btnPause.onclick = () => {
  window.clearInterval(id);
};

btnPlay.onclick = () => {
  id = setInterval(run, time);
};

btnSlow.onclick = () => {
  window.clearInterval(id);
  time = 300;
  id = setInterval(run, time);
};

btnNormal.onclick = () => {
  window.clearInterval(id);
  time = 100;
  id = setInterval(run, time);
};

btnFast.onclick = () => {
  window.clearInterval(id);
  time = 0;
  id = setInterval(run, time);
};
